<template>
  <!-- automatic -->
  <custom-vs-upload
    single-upload
    multiple
    :showUploadButton="_showUploadButton"
    :headers="_headerUpload"
    :oldData="oldDate"
    :readOnly="readOnly"
    fileName="file[]"
    text="เลือกไฟล์ หรือ ลากวาง"
    action="/api/attached"
    @on-success="successUpload"
    @on-delete="deleteUpload"
    @on-error="erroreUpload"
    ref="uploader"
  />
</template>

<script>
import axios from 'axios'
export default {
  name: 'attached',
  component: {
    axios
  },
  props: {
    attached_id: {
      default: '',
      type: String
    },
    model: {
      default: '',
      type: String
    },
    readOnly: {
      default: false,
      type: Boolean
    }
  },
  computed: {
    _showUploadButton () {
      return !!(this.id && this.model)
    },
    _headerUpload () {
      return {
        Authorization: `${this.$store.getters.getToken}`
      }
    }
    // _dataUpload () {
    //   this.loadData()
    //   return { id: this.id, model: this.model }
    // }
  },
  data () {
    return {
      oldDate: null,
      id:null
    }
  },
  mounted () {
    this.loadData()
  },

  methods: {
    async submit () {
      console.log(this.id, this.model)
      // this.dataUpload =
      if (this.id && this.model) {
        // setInterval(async () => {

        //   const result = await this.$refs.uploader.upload('all')
        //   return result
        // }, 0)
        this.$refs.uploader.data = { id: this.id, model: this.model }
        const result = await this.$refs.uploader.upload('all')
        return result
      } else {
        this.$vs.notify({
          color: 'danger',
          title: 'อัพโหลด ไม่สำเร็จ',
          text: 'ไม่มีการกำหนด ID, Model'
        })
      }
    },
    async loadData () {
      if (!this.attached_id) {
        setTimeout(() => {
          this.loadData()
        }, 500)
        return
      }
      if (this.attached_id) {
        try {
          const { data } = await this.$http.get(`/api/attached/${this.attached_id}`)
          this.oldDate = data.data
        } catch (error) {
          throw error
        }
      }
    },
    successUpload (data) {
      console.log('successUpload')
      console.log(data)
      this.$vs.notify({
        color: 'success',
        title: 'อัพโหลด สำเร็จ',
        text: ''
      })
    },
    deleteUpload (data) {
      console.log('deleteUpload')
      console.log(data)
      this.$vs.notify({
        color: 'danger',
        title: 'ลบไฟล์ที่อัพโหลด สำเร็จ',
        text: ''
      })
    },
    erroreUpload (data) {
      console.log('erroreUpload')
      console.log(data)
      this.$vs.notify({
        color: 'waring',
        title: 'ผิดพลาด ไม่รองรับไฟล์นามสกุลไฟล์นี้',
        text: ''
      })
    }
  }
}
</script>

