<template>
  <div class="con-upload">
    <!-- <pre>{{getFilesFilter}}</pre> -->
    <!-- <pre>{{oldData}}</pre> -->
    <!-- <pre>{{ srcs }}</pre> -->
    <view-upload v-if="viewActive" :src="viewSrc" />

    <div class="con-img-upload">
      <!-- รูปเก่า -->
      <div
        class="img-upload"
        v-for="(item, index) in oldData"
        :key="`old-${index}`"
      >
        <button v-if="!readOnly" class="btn-x-file" type="button" @click="removeOldFile(index)">
          <i translate="no" class="material-icons notranslate"> clear </i>
        </button>
        <img
          v-if="item.type === 'image'"
          :style="{
            maxWidth: item.width < item.height ? '100%' : 'none',
            maxHeight: item.width >= item.height ? '100%' : 'none',
          }"
          :src="`/api${item.full_path}`"
          @touchend="viewImage(`api${item.full_path}`, $event)"
          @click="viewImage(`api${item.full_path}`, $event)"
        />
        <img
          v-else-if="item.type === 'pdf'"
          src="data:image/svg+xml;base64,"
        />
        <img
          v-else-if="item.type === 'file'"
          src="data:image/svg+xml;base64,"
        />
      </div>
      <!-- สิ้นสุดรูปเก่า -->
      <div
        v-for="(img, index) in getFilesFilter"
        :class="{
          fileError: img.error,
          removeItem: itemRemove.includes(index),
        }"
        :key="index"
        class="img-upload"
      >
        <button class="btn-x-file" type="button" @click="removeFile(index)">
          <i translate="no" class="material-icons notranslate"> clear </i>
        </button>
        <button
          v-if="showUploadButton"
          :class="{
            'on-progress': img.percent,
            'ready-progress': img.percent >= 100,
          }"
          :style="{
            height: `${img.percent}%`,
          }"
          class="btn-upload-file"
          @click="upload(index)"
        >
          <i translate="no" class="material-icons notranslate">
            {{
              img.percent >= 100
                ? img.error
                  ? "report_problem"
                  : "cloud_done"
                : "cloud_upload"
            }}
          </i>
          <span>{{ img.percent }} %</span>
        </button>
        <img
          v-if="img.src"
          :style="{
            maxWidth: img.orientation == 'h' ? '100%' : 'none',
            maxHeight: img.orientation == 'w' ? '100%' : 'none',
          }"
          :key="index"
          :src="img.src"
          @touchend="viewImage(img.src, $event)"
          @click="viewImage(img.src, $event)"
        />
        <h4 v-if="!img.src" class="text-archive">
          <i translate="no" class="material-icons notranslate"> description </i>
          <span>
            {{ img.name }}
          </span>
        </h4>
      </div>

      <div
        v-if="!readOnly"
        :class="{
          'on-progress-all-upload': percent != 0,
          'is-ready-all-upload': percent >= 100,
          'disabled-upload':
            $attrs.hasOwnProperty('disabled') || limit
              ? srcs.length - itemRemove.length >= Number(limit)
              : false,
        }"
        class="con-input-upload"
      >
        <input
          ref="fileInput"
          v-bind="$attrs"
          :disabled="
            $attrs.disabled || limit
              ? srcs.length - itemRemove.length >= Number(limit)
              : false
          "
          type="file"
          @change="getFiles"
        />
        <span class="text-input">
          {{ text }}
        </span>
        <span
          :style="{
            width: `${percent}%`,
          }"
          class="input-progress"
        >
        </span>
        <button
          v-if="showUploadButton"
          :disabled="filesx.length == 0"
          type="button"
          title="Upload"
          class="btn-upload-all vs-upload--button-upload"
          @click="upload('all')"
        >
          <i translate="no" class="material-icons notranslate">
            cloud_upload
          </i>
        </button>
      </div>
    </div>
  </div>
</template>
<script>
import viewUpload from './viewUpload'
let lastTap = 0
export default {
  name: 'CustomVsUpload',
  components: {
    viewUpload
  },
  inheritAttrs: false,
  props: {
    fileName: {
      default: null,
      type: String
    },
    text: {
      default: 'Upload File',
      type: String
    },
    textMax: {
      default: 'Maximum of files reached',
      type: String
    },
    limit: {
      default: null,
      type: [Number, String]
    },
    action: {
      default: null,
      type: String
    },
    headers: {
      default: null,
      type: Object
    },
    // data: {
    //   default: null,
    //   type: Object
    // },
    oldData: {
      default: () => [],
      type: Array
    },
    automatic: {
      default: false,
      type: Boolean
    },
    showUploadButton: {
      default: true,
      type: Boolean
    },
    singleUpload: {
      default: false,
      type: Boolean
    },
    readOnly: {
      default: false,
      type: Boolean
    }
  },
  data: () => ({
    data:{},
    inputValue: null,
    type: null,
    srcs: [],
    filesx: [],
    itemRemove: [], //index ว่าลบตัวไหนไปแล้วบ้าง
    percent: 0,
    viewActive: false,
    viewSrc: null
  }),
  computed: {
    getFilesFilter () {
      const files = this.srcs.filter((item) => {
        return !item.remove
      })
      return files
    },
    postFiles () {
      let postFiles = Array.prototype.slice.call(this.filesx)
      postFiles = postFiles.filter((item) => {
        return !item.hasOwnProperty('remove')
      })
      return postFiles.length
    }
  },
  watch: {
    percent () {
      if (this.percent >= 100) {
        this.srcs.forEach((file) => {
          file.percent = 100
        })
        setTimeout(() => {
          this.percent = 0
        }, 1000)
      }
    }
  },
  methods: {
    viewImage (src, evt) {
      const timeout = 1000

      let eventx = 'ontouchstart' in window
      if (window.DocumentTouch && document instanceof window.DocumentTouch) {
        eventx = 'touchstart'
      } else {
        eventx = 'click'
      }

      if (eventx === 'click') {
        this.viewActive = true
        this.viewSrc = src
      } else if (evt.type === 'touchend') {
        const currentTime = new Date().getTime()
        const tapLength = currentTime - lastTap
        clearTimeout(timeout)
        if (tapLength < 500 && tapLength > 0) {
          this.viewActive = true
          this.viewSrc = src
          event.preventDefault()
        }
        lastTap = currentTime
      }
    },
    removeOldFile (index) {
      // ลบไฟล์เก่าจากฐานข้อมูล
      const remove_url = `${this.action}/${this.oldData[index].id}`

      // ส่ง requst ไปลบ
      const self = this
      const xhr = new XMLHttpRequest()

      xhr.onerror = function error (e) {
        self.$emit('on-error', e)
      }

      xhr.onload = function onload (e) {
        if (xhr.status < 200 || xhr.status >= 300) {
          self.$emit('on-error', e)
        } else {
          //ส่ง request delete สำเร็จ
          self.oldData.splice(index, 1)
          self.$emit('on-delete', {})
        }
      }
      xhr.withCredentials = true

      xhr.open('DELETE', remove_url, true)

      const headers = this.headers || {}

      for (const head in headers) {
        if (headers.hasOwnProperty(head) && headers[head] !== null) {
          xhr.setRequestHeader(head, headers[head])
        }
      }

      const formData = new FormData()
      xhr.send(formData)
    },
    removeFile (index) {
      // ลบไฟล์ที่ยังไม่อัพ หรือ ที่เพิ่งอัพไปใหม่
      console.log('removeFile')
      console.log(this.srcs[index])

      // ส่ง requst ไปลบด้วย ถ้ากรณีมี remove_url
      if (this.srcs[index].remove_url) {
        const self = this
        const xhr = new XMLHttpRequest()

        xhr.onerror = function error (e) {
          self.$emit('on-error', e)
          if (typeof index === 'number') {
            self.srcs[index].error = true
          }
        }

        xhr.onload = function onload (e) {
          if (xhr.status < 200 || xhr.status >= 300) {
            self.$emit('on-error', e)
            if (typeof index === 'number') {
              self.srcs[index].error = true
            }
          } else {
            //ส่ง request delete สำเร็จ
            self.itemRemove.push(index)
            self.$emit('on-delete', self.filesx[index])
          }
        }
        xhr.withCredentials = true

        xhr.open('DELETE', this.srcs[index].remove_url, true)

        const headers = this.headers || {}

        for (const head in headers) {
          if (headers.hasOwnProperty(head) && headers[head] !== null) {
            xhr.setRequestHeader(head, headers[head])
          }
        }

        const formData = new FormData()
        xhr.send(formData)
      } else {
        //เก็บ index ลง itemRemove เพื่อจะได้ซ่อนออกไปจากลิสรายการ
        this.itemRemove.push(index)
        this.$emit('on-delete', this.filesx[index])
      }
    },
    getFiles (e) {
      this.$emit('update:vsFile', e.target.value)
      const _this = this
      function switchImage (image, orientation) {
        _this.srcs.push({
          src: image.src,
          orientation,
          type: _this.typex,
          percent: null,
          error: false,
          remove: null,
          remove_url: null
        })
      }
      function uploadImage (e) {
        let orientation = 'h'
        const image = new Image()
        image.src = e.target.result
        image.onload = function () {
          if (this.width > this.height) {
            orientation = 'w'
          }
          switchImage(this, orientation)
        }
      }

      const files = e.target.files
      let count = this.srcs.length - this.itemRemove.length
      for (const file in files) {
        if (files.hasOwnProperty(file)) {
          if (this.limit) {
            count++
            if (count > Number(this.limit)) {
              break
            }
          }

          const reader = new FileReader()
          const filex = files[file]
          if (/image.*/.test(filex.type)) {
            this.typex = 'image'
            this.filesx.push(filex)
            reader.onload = uploadImage
            reader.readAsDataURL(filex)
          } else if (/video.*/.test(filex.type)) {
            this.typex = 'video'
            this.filesx.push(filex)
            _this.srcs.push({
              src: null,
              name: filex.name,
              type: 'video',
              percent: null,
              error: false,
              remove: null
            })
          } else {
            this.filesx.push(filex)
            _this.srcs.push({
              src: null,
              name: filex.name,
              percent: null,
              error: false,
              remove: null
            })
          }
          this.$emit('change', e.target.value, this.filesx)
        }
      }
      const input = this.$refs.fileInput
      input.type = 'text'
      input.type = 'file'

      // หลังจากเลือกไฟล์แล้วถ้า automatic = true จะอัพไฟล์ทันที
      if (this.automatic) {
        this.upload('all')
      }
    },
    async upload (index) {
      const formData = new FormData()
      let postFiles = Array.prototype.slice.call(this.filesx)
      if (typeof index === 'number') {
        postFiles = [postFiles[index]]
      } else if (index === 'all') {
        postFiles = postFiles.filter((item) => {
          // หลักการทำงานตรงนี้ไม่ถูกเพราะเหมือนจะ remove = null
          return !item.hasOwnProperty('remove')
        })
      }

      const data = this.data || {}
      for (const key in data) {
        formData.append(key, data[key])
      }
      if (this.singleUpload) {
        postFiles.forEach((filex) => {
          for (const key in data) {
            formData.append(key, data[key])
          }
          formData.append(this.fileName, filex, filex.name)
        })
      } else {
        postFiles.forEach((filex) => {
          formData.append(this.fileName, filex, filex.name)
        })
      }
      const result = await this.uploadx(index, formData)
      return result
    },
    //อัพโหลดไฟล์ขึ้น url
    uploadx (index, formData) {
      const self = this

      return new Promise((resolve, reject) => {
        const xhr = new XMLHttpRequest()

        xhr.onerror = function error (e) {
          self.$emit('on-error', e)
          if (typeof index === 'number') {
            self.srcs[index].error = true
          }
        }

        xhr.onload = function onload (e) {
          // console.log('1')
          if (self.filesx.length === 0) {
            resolve('ไม่มีข้อมูล')
          }

          const res = JSON.parse(e.currentTarget.response)
          if (xhr.status < 200 || xhr.status >= 300) {
            self.$emit('on-error', e)
            if (typeof index === 'number') {
              self.srcs[index].error = true
            } else if (index === 'all') {
              self.srcs.map((obj) => {
                obj.error = true
              })
            }
            reject(res.data)
          } else {
            console.log(`onload ${index}`)
            // เตรียมลิงค์ไว้สั่งลบ
            console.log(res.data)
            res.data.map((obj, index) => {
              self.srcs[index].remove_url = `${self.action}/${obj.id}`
            })
            self.$emit('on-success', e)
            resolve(res.data)
          }
        }

        if (xhr.upload) {
          xhr.upload.onprogress = function progress (e) {
            if (e.total > 0) {
              const percent = (e.loaded / e.total) * 100
              if (typeof index === 'number') {
                self.srcs[index].percent = Math.trunc(percent)
              } else {
                self.percent = Math.trunc(percent)
              }
            }
          }
        }

        xhr.withCredentials = true

        xhr.open('POST', this.action, true)

        const headers = this.headers || {}

        for (const head in headers) {
          if (headers.hasOwnProperty(head) && headers[head] !== null) {
            xhr.setRequestHeader(head, headers[head])
          }
        }

        xhr.send(formData)
      })
    }
  }
}
</script>
