<template>
  <div>
    <vs-input
      class="inputx tree-search-input float-left mr-2"
      placeholder="Search..."
      v-model.lazy="searchword"
    />
    <vs-button color="primary" type="filled" @click="search" class="mb-3"
      >ค้นหา</vs-button
    >
    <!-- node-click, node-select ทำงานเหมือนกันเลย แต่ node-select ได้ข้อมูลเยอะกว่า -->
    <v-tree
      ref="tree"
      :radio="true"
      :canDeleteRoot="false"
      :data="treeData"
      :tpl="tpl"
      :halfcheck="false"
      :selectAlone="true"
      @node-click="nodeClick"
      @node-select="nodeSelect"
      @node-expand="nodeExpand"
    />
    <!-- <pre>{{tempExpand}}</pre> -->
  </div>
</template>

<script>
import { VTree, VSelectTree } from 'vue-tree-halower'
export default {
  name: 'custom-tree',
  components: {
    VTree,
    VSelectTree
  },
  props: {
    name: {
      default: '',
      type: String
    },
    url: {
      default: '',
      type: String
    }
  },
  data () {
    return {
      tempExpand:[],
      searchword: '',
      treeData: []
      // treeData: [
      //   {
      //     title: 'node1',
      //     expanded: true,
      //     children: [
      //       {
      //         title: 'node 1-1',
      //         expanded: false,
      //         children: [
      //           {
      //             title: 'node 1-1-1',
      //             value: '1-1-1'
      //           },
      //           {
      //             title: 'node 1-1-2',
      //             value: '1-1-2'
      //           },
      //           {
      //             title: 'node 1-1-3',
      //             value: '1-1-3'
      //           }
      //         ]
      //       },
      //       {
      //         title: 'node 1-2',
      //         children: [
      //           {
      //             title: '<span style=\'color: red\'>node 1-2-1</span>'
      //           },
      //           {
      //             title: '<span style=\'color: red\'>node 1-2-2</span>'
      //           }
      //         ]
      //       }
      //     ]
      //   }
      // ]
    }
  },
  // created () {
  //   window.addEventListener('unload', this.beforeRefresh)
  // },
  mounted () {
    this.loadData()
  },
  // destroyed () {
  //   this.$destroy()
  // },
  methods: {
    // beforeRefresh (event) {
    //   console.log('beforeRefresh')
    // },
    async loadData () {
      try {
        const { data } = await this.$http.get(this.url)
        this.treeData = data.data
        this.loadExpand()
      } catch (error) {
        console.error(error)
        return false
      }
    },
    refresh () {
      this.loadData()
    },
    async loadExpand () {
      try {
        this.flatten(this.treeData)
      } catch (error) {
        console.error(error)
        return false
      }
    },
    flatten (data) {
      data.forEach((element) => {
        if (Array.isArray(element.children) && element.children.length !== 0) {
          this.flatten(element.children)
        }
        if (this.tempExpand.includes(element.id)) {
          element.expanded = true
        }
      })
    },
    nodeClick (node) {
      // สร้างเป็น obj ใหม่ และลบ children ออกก่อนจะส่งกลับ
      const tempNode = { ...node }
      delete tempNode.children

      this.$emit('onClick', tempNode)
    },
    nodeSelect (node, selected, position) {
      // สร้างเป็น obj ใหม่ และลบ children ออกก่อนจะส่งกลับ
      const tempNode = { ...node }
      delete tempNode.children

      this.$emit('onSelect', tempNode, selected, position)
    },
    nodeExpand (node, expand) {
      // ตรวจ expand เป็น true|false เพื่อ เพิ่ม/ลบ tempExpand
      if (expand) {
        this.tempExpand.push(node.id)
      } else {
        const temp = this.tempExpand.filter((tempNode) => {
          return tempNode !== node.id
        })
        this.tempExpand = temp
      }
    },
    deleteNode (node, parent, index) {
      // ถ้าเป็นหัวข้อที่มีลูกได้จะไม่ให้ลบ แต่ถ้าไม่มีลูกจะให้ลบได้
      if (Array.isArray(node.children) && node.children.length) {
        this.$vs.notify({
          color: 'danger',
          title: 'ไม่สามารถลบ หัวข้อที่มีลูกได้',
          text: ''
        })
        return ''
      }

      // สร้างเป็น obj ใหม่ และลบ children ออกก่อนจะส่งกลับ
      const tempNode = { ...node }
      delete tempNode.children

      this.$emit('onDelete', tempNode, parent, index)
      return ''
    },
    newNode (node) {
      if (!node.id) {
        this.$vs.notify({
          color: 'danger',
          title: 'ไม่สามารถเพิ่มลูกได้ เพราะยังไม่ได้บันทึก',
          text: ''
        })
        return ''
      }
      const temp_ref_id = node.id
      this.$refs.tree.addNode(node, { title: 'sync node', ref_id:temp_ref_id })
    },
    removeNode (node, parent, index) {
      this.$refs.tree.delNode(node, parent, index)
    },
    search () {
      this.$refs.tree.searchNodes(this.searchword)
    },
    tpl (...args) {
      const { 0: node, 2: parent, 3: index } = args
      // const temp_ref_id = parent ? parent.id : node.id
      let titleClass = node.selected ? 'node-title node-selected' : 'node-title'
      if (node.searched) titleClass += ' node-searched'
      return (
        <span>
          <span
            class={titleClass}
            domPropsInnerHTML={node.title}
            onClick={() => {
              this.$refs.tree.nodeSelected(node)
            }}
          ></span>
          <button
            class="btn-async text-warning border-none cursor-pointer px-2 py-1 mr-2 rounded"
            onClick={() => this.newNode(node)
            }
          >
            เพิ่มลูก
          </button>
          <button
            class="btn-delete text-danger border-none cursor-pointer px-2 py-1 rounded"
            onClick={() => this.deleteNode(node, parent, index)}
          >
            ลบ
          </button>
        </span>
      )
    }
  }
}
</script>

<style lang="scss">
@import "@/assets/scss/vuexy/extraComponents/tree.scss";

button.btn-async {
  background: rgba(var(--vs-warning), 0.15);
}

button.btn-delete {
  background: rgba(var(--vs-danger), 0.15);
}
</style>
