<template>
  <div :class="`vx-col w-${_col}`">
    <v-select-tree
      ref="selecttree"
      pleasechoosetext="กรุณาเลือก"
      searchtext="ค้นหา"
      :multiple="false"
      :radio="true"
      :data="treeData"
      @node-select="nodeSelect"
    />
  </div>
</template>

<script>
import { VTree, VSelectTree } from 'vue-tree-halower'
export default {
  name: 'custom-tree-select',
  components: {
    VTree,
    VSelectTree
  },
  props: {
    name: {
      default: '',
      type: String
    },
    url: {
      default: '',
      type: String
    },
    col: {
      default: 1,
      type: Number
    },
    value: {
      default: '',
      type: String
    }
  },
  computed: {
    _col () {
      if (this.col === '12') {
        return 'full'
      } else {
        return `${this.col}/12`
      }
    }
  },
  watch: {
    inputData: {
      immediate: true,
      handler (val) {
        this._handlerInput(val)
      }
    },
    value: {
      immediate: true,
      handler (val) {
        if (val) {
          this.updateSelctNode()
        }
      }
    }
  },
  data () {
    return {
      nodeData: {},
      inputData: '',
      treeData: []
    }
  },
  mounted () {
    this.loadData()
  },
  methods: {
    async loadData () {
      try {
        const { data } = await this.$http.get(this.url)
        this.treeData = data.data
      } catch (error) {
        console.error(error)
        return false
      }
    },
    refresh () {
      this.loadData()
    },
    updateSelctNode () {
      this.flatten(this.treeData)
    },
    flatten (data) {
      data.forEach((element) => {
        if (Array.isArray(element.children) && element.children.length !== 0) {
          this.flatten(element.children)
        }
        if (element.id === this.value) {
          this.$refs.selecttree.selectedItems = [element.title]
          element.selected = true
        }
      })
    },
    nodeSelect (node, selected, position) {
      console.log(node, selected, position)
      this.inputData = node.id
      this.$refs.selecttree.open = false

      this.nodeData = node
      this.$emit('node-select', node)
    },
    _handlerInput (value) {
      if (value) {
        this.$emit('input', this.inputData)
      } else {
        this.$emit('input', value)
      }
    }
  }
}
</script>

