import router from '@/router'
import axios from '@/axios'
const state = {
  token: localStorage.getItem('token') || null
}

const getters = {
  getToken: state => {
    return state.token
  }
}

const actions = {
  setToken ({ commit }, token) {
    return new Promise((resolve, reject) => {
      const header_token = `Bearer ${token}`
      commit('SET_TOKEN', header_token)
      resolve(header_token)
    })
  },
  checkAuth ({ commit, getters }) {
    return new Promise((resolve, reject) => {
      const header_token = getters.getToken
      axios.get('/api/info', {
        headers: { Authorization: header_token }
      })
        .then(({ data }) => {
          const userInfo =  {
            uid         : data.data.id,          // From Auth
            displayName : data.data.name // From Auth
            // photoURL    : require('@/assets/images/portrait/small/avatar-s-11.jpg'), // From Auth
          }
          commit('UPDATE_USER_INFO', userInfo)

          resolve(data)
        })
        .catch(err => {
          commit('DEL_TOKEN')
          reject(err)
        })
    })
  },
  async logout ({ commit, getters }) {
    try {
      const header_token = getters.getToken
      await axios.post(
        '/api/logout',
        {},
        {
          headers: { Authorization: header_token }
        }
      )
      commit('DEL_TOKEN')
      router.push('/login')
    } catch (error) {
      commit('DEL_TOKEN')
      router.push('/login')
      throw error
    }
  }
}

const mutations = {
  SET_TOKEN (state, token) {
    localStorage.setItem('token', token)
    state.token = token
  },
  DEL_TOKEN (state) {
    localStorage.removeItem('token')
    state.token = null
  }
}

export default {
  state,
  getters,
  actions,
  mutations
}
