<template>
  <div :class="`vx-col w-${_col}`">

    <vs-input
      :label-placeholder="label"
      :danger="_error"
      :danger-text="`กรุณากรอก ${_error_text}`"
      type="search"
      v-model="inputData"
      @input="_handlerInput"
      :name="name"
      class="my-5 w-full"
    />
  </div>
</template>

<script>
export default {
  name: 'form-input',
  props: {
    label: {
      default: '',
      type: String
    },
    value: {
      default: '',
      type: String | Number
    },
    col: {
      default: 1,
      type: Number
    },
    dataVvAs: {
      default: '',
      type: String
    }
  },
  watch: {
    value: {
      immediate: true,
      handler (val) {
        this.dirty = true
        this.inputData = val
      }
    }
  },
  computed: {
    _col () {
      if (this.col === '12') {
        return 'full'
      } else {
        return `${this.col}/12`
      }
    },
    _name () {
      return `name_${Math.floor(Math.random() * 100000)}`
    },
    _error () {
      return this.errors.items.find((f) => f.field === this.name)
    },
    _error_text () {
      return this.dataVvAs || this.label
    }
  },
  data () {
    return {
      name: `name_${Math.floor(
        Math.random() * (0 - 999999 + 999999999999)
      )}${1}`,
      inputData: ''
    }
  },
  mounted () {},
  methods: {
    _handlerInput (value) {
      if (value) {
        this.$emit('input', this.inputData)
      } else {
        this.$emit('input', value)
      }
    }
  }
}
</script>

<style>
.vs-input--input.hasValue + .vs-placeholder-label,
.vs-input--input:focus + .vs-placeholder-label {
  font-size: 0.9rem;
}
.con-text-validation {
   font-size: .8rem;
}
</style>
