<template>
  <div :class="`vx-col w-${_col}`">
    <vue-tags-input
      v-model="tag"
      placeholder="กรอกแท็ก"
      :tags="tags"
      @tags-changed="_handlerTag"
      :autocomplete-items="filteredItems"
    />
  </div>
</template>

<script>
export default {
  name: 'form-tag',
  props: {
    label: {
      default: '',
      type: String
    },
    value: {
      default: () => [],
      type: Array
    },
    col: {
      default: 1,
      type: Number
    }
  },
  watch: {
    value (val) {
      this.tags = val
    }
  },
  computed: {
    _col () {
      if (this.col === '12') {
        return 'full'
      } else {
        return `${this.col}/12`
      }
    },
    filteredItems () {
      return this.autocompleteItems.filter(i => {
        return i.text.toLowerCase().indexOf(this.tag.toLowerCase()) !== -1
      })
    }
  },
  data () {
    return {
      name: `name_${Math.floor(Math.random() * (0 - 999999 + 999999999999))}`,
      tag: '',
      tags: [],
      autocompleteItems:[]
    }
  },
  mounted () {
    this.loadData()
    this.setData()
  },
  methods: {
    setData () {
      if (!this.value) {
        setTimeout(() => {
          this.setData()
        }, 500)
        return
      }
      console.log('setData', this.value)
      this.tags = this.value
    },
    async loadData () {
      try {
        const resp = await this.$http.get('/api/helper/alltag')
        this.autocompleteItems = resp.data.data
      } catch (error) {
        console.error(error)
        return false
      }
    },
    _handlerTag (value) {
      if (value) {
        this.$emit('input', value)
      } else {
        this.$emit('input', this.tags)
      }
    }
  }
}
</script>

<style>
.vue-tags-input .ti-input {
  padding: 4px 10px;
  transition: border-bottom 200ms ease;
  border-radius: 5px;
}
.vue-tags-input.ti-focus .ti-input {
  border: 1px solid #0068b7;
  border-radius: 5px;
}
.vue-tags-input .ti-autocomplete {
  color: #4e5e6d;
  background: #f4f7f8;
  border: 1px solid #a4adb5;
  border-top: none;
  border-radius: 5px;
}
.vue-tags-input .ti-tag.custom-class {
  background: transparent;
  border: 1px solid #0068b7;
  color: #0068b7;
  margin-right: 4px;
  font-size: 13px;
  border-radius: 5px;
}
</style>
